<!-- eslint-disable max-len -->
<template>
  <section class="overflow-x-auto">
    <table
      class="table table-bordered border-dark table-success table-striped-columns table-striped-columns"
    >
      <thead>
        <tr>
          <th scope="col">#</th>
          <template v-for="(item, index) in orderList" :key="index">
            <th scope="col">
              <button
                class="btn btn-outline-success"
                @click="this.$refs.offcan.showOff(item)"
              >
              ({{item.company.companyName}}){{item.address}}
              </button>
            </th>
          </template>
        </tr>
      </thead>
      <tbody>
        <template v-for="(item, index) in workerList" :key="index">
          <tr>
            <th scope="row">{{ item.name }}</th>
            <td>10</td>
          </tr>
        </template>
      </tbody>
    </table>
  </section>
  <Offcan ref="offcan" :workerList="workerList" />
</template>

<script>
import { apiGetWorkers, apiGetOrders } from '@/assets/javascript/api';
import Offcan from '@/components/bootstrap/OffcansModel.vue';

export default {
  data() {
    return {
      workerList: [],
      orderList: [],
    };
  },
  components: {
    Offcan,
  },
  methods: {
    getOrders() {
      apiGetOrders()
        .then((res) => {
          this.orderList = res.data.data;
        })
        .catch((err) => {
          console.log(err);
        });
    },
    getWorkers() {
      apiGetWorkers()
        .then((res) => {
          this.workerList = res.data.data;
        })
        .catch((err) => {
          console.log(err);
        });
    },
  },
  mounted() {
    this.getOrders();
    this.getWorkers();
  },
};
</script>
